                                                            </div>
                                                            <div class="ware-edit">
                                                                <p class="float-right mr-5 mt-1">
                                                                    <a type="button" v-on:click="editTpsafFacility(location.warehouse.name)"><img src="@/assets/images/icons/edit-gray.png" class="mr-1"><span class="text-primary mr-2"><b>Edit</b></span></a>
                                                                    <a type="button" v-on:click="removeTpsafFacility(location.warehouse.name)"><img src="@/assets/images/icons/cross-small.png" class="mr-1"><span class="text-primary"><b>Remove</b></span></a>
                                                                </p>
                                                            </div>
                                                        </div>
                                                        <div v-if="facilitiesListError">
                                                            <span class="text-danger" v-if="!$v.facilities.checkNullOrEmpty">Add atleast one Warehouse</span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="row w-100"><div class="col-md-12"><button name="save" value="SAVE" type="button" class="btn btn-primary float-right  pl-3 pr-3 p-2" v-on:click="saveFacilityWarehouse()"><img src="@/assets/images/icons/save-new.png" class="mr-2 ic-wd" />SAVE</button></div></div>
                                            </form>
                                        </div>
                                    </div>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                        <button :disabled="!showPreview()" class="btn btn-primary btn-lg float-right mb-4 pl-5 pr-5 p-3 mt-3 mr-3" name="preview" value="PREVIEW" type="button" v-on:click="submitAllDetails();">SUBMIT</button>
                        <button class="btn btn-primary btn-lg float-right mb-4 pl-5 pr-5 p-3 mt-3 mr-3" name="preview" value="PREVIEW" type="button" v-on:click="clearData()">Cancel</button>
                    </div>
                </div>
            </div>
        </b-collapse>
        <!-- END: Card DATA-->
        <br clear="all" />
        <div class="card">
            <div class="list-filter">
                <div class="btn-group">
                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="ALL" checked :value="allTpsafCount">
                    <label type="button" class="btn btn-outline-dark rounded-btn mr-2 mob-mar" for="ALL" @click="bindTpsaf('ALL')">ALL({{allTpsafCount}})</label>
                </div>
                <div class="btn-group">
                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="Active" :value="activeTpsafCount">
                    <label type="button" class="btn btn-outline-success rounded-btn mr-2 mob-mar" for="Active" @click="bindTpsaf('Active')">Active({{activeTpsafCount}})</label>
                </div>
                <div class="btn-group">
                    <input type="radio" name="radios" autocomplete="off" class="custom-control-input" id="InActive" :value="inActiveTpsafCount">
                    <label type="button" class="btn btn-outline-danger rounded-btn mr-2 mob-mar" for="InActive" @click="bindTpsaf('InActive')">Inactive({{inActiveTpsafCount}})</label>
                </div>
            </div>
            <table id="tab" class="table tpsaf">
                <thead>
                    <tr>
                        <th class="p-3">Name</th>
                        <th class="p-3">TIN Number</th>
                        <th class="p-3">Registration Date</th>
                        <th class="p-3">Status</th>
                        <th class="p-3">Actions</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(tps, index) in getTpsaf" :key="index">
                        <td class="p-2">{{tps.name}}</td>
                        <td class="p-2">{{tps.tinNumber}}</td>
                        <td class="p-2">{{tps.registerationDate | FormatingDate }}</td>
                        <td v-if="tps.status===1" class="text-success p-2">Active</td>
                        <td v-else class="text-danger p-2">Inactive</td>
                        <td class="p-2">
                            <a v-on:click="viewTpsafDetails(tps.id)" class="text-primary"> <img src="@/assets/images/icons/view.png" /> View </a>
                            <a v-on:click="editTpsafDetails(tps.id)" class="text-primary p-1"> <img src="@/assets/images/icons/edit-small.png" /> Edit </a>
                            <a v-if="tps.status===1" class="text-primary p-1" v-on:click="getactiveDeactiveStatus(tps.id, 2)"><img src="@/assets/images/icons/activate.png" /> Deactivate</a>
                            <a v-else class="text-primary p-1" v-on:click="getactiveDeactiveStatus(tps.id, 1)"><img src="@/assets/images/icons/deactivate-new.png" /> Activate</a>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <b-modal id="deactiveTpsaf" hide-footer hide-header centered>
            <template v-slot:modal-title>
                <h5 class="modal-title">TPSAF</h5>
            </template>
            <div class="d-block">
                <div class="modal-body mt-4 mb-3" align="center">
                    <div class="mt-4 mb-4">
                        <img v-if="activeDeactiveStatus == 1" src="@/assets/images/icons/tick.png">
                        <img v-else src="@/assets/images/icons/warning-message-icon.png">
                    </div>
                    <h3>Are you sure</h3>
                    <p v-if="activeDeactiveStatus == 1" class="mb-5">you want to activate this TPSAF?</p>
                    <p v-else class="mb-5">you want to deactivate this TPSAF?</p>
                    <button type="button" class="btn btn-outline-primary btn-lg mr-3" @click="$bvModal.hide('deactiveTpsaf')">Cancel</button>
                    <button type="button" class="btn btn-primary btn-lg" v-on:click="activeDeactivetpsafStatus();">Ok</button>
                </div>
            </div>
        </b-modal>
    </div>
</template>

<script>
    import { required, alphaNum, email, sameAs } from 'vuelidate/lib/validators';
    import { checkNullOrEmpty, checkEmptyNumber, checkEmailDuplicate } from '@/Validations';
    import toast from '@/toast';
    export default {
        data() {
            return {
                tpsaf: this.initializeTPSAF(),
                objfacilities: this.initializeFacility(), facilities: [], tpsafError: false,
                tpsafConfirmEmail: '', facilityWareHouseError: false, facilityConfirmEmailId: '', facilitiesListError: false,
                accordion1: false, accordion2: false, objTpsaf: '', showAccordion1: false, showAccordion2: false,
                showNewFacility: false, showNewTPSAF: false, tpsafCountry: '', tpsafRegion: '', tpsafFacilityCountry: '',
                tpsafFacilityRegion: '', regions: [], tpsafList: [], allTpsafCount: 0, activeTpsafCount: 0, inActiveTpsafCount: 0, activeDeactiveStatus: 1, deactiveId: ''
            }
        },
        async mounted() {
            await this.$store.dispatch("getCountriesAsync");
            await this.$store.dispatch("getTpsafAsync");
            this.bindTpsaf('ALL');
        },
        computed: {
            getTpsaf() {
                return this.tpsafList;
            }
        },
        filters: {
            FormatingDate: function (value) {
                if (value == null || value == '') return '';
                var date = new Date(value);
                var months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
                return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()] + "-" + date.getFullYear();
            }
        },
        validations: {
            tpsaf: {
                name: { required },
                tinNumber: {
                    required, alphaNum,
                    checkDuplicate(value) {
                        if (this.tpsaf.id !== 0) return true;
                        return !(this.$store.getters.tpsaf.filter(element => element.tinNumber.toLowerCase() === value.toLowerCase()).length > 0);
                    }
                },
                exciseLicenseNumber: {
                    required, alphaNum,
                    checkDuplicate(value) {
                        if (this.tpsaf.id !== 0) return true;
                        return !(this.$store.getters.tpsaf.filter(element => element.exciseLicenseNumber.toLowerCase() === value.toLowerCase()).length > 0);
                    }
                },
                contact: {
                    name: { required },
                    email: {
                        required, email,
                        checkEmailDuplicate(value) {
                            if (this.tpsaf.id !== 0) return true;
                            return checkEmailDuplicate(value);
                        }
                    },
                    phone: { required },
                    countryCode: { required },
                },
                address: {
                    description: { required },
                    postalAddress: { required }
                }
            },
            tpsafConfirmEmail: { required, email, sameAs: sameAs(function () { return this.tpsaf.contact.email }) },
            objfacilities: {
                warehouse: {
                    name: { required },
                    address: {
                        latitude: { checkEmptyNumber },
                        longitude: { checkEmptyNumber },
                        description: { required },
                        postalAddress: { required }
                    },
                    contact: {
                        name: { required },
                        phone: { required },
                        countryCode: { required },
                        email: {
                            required, email,
                            checkEmailDuplicate(value) {
                                if (this.tpsaf.id !== 0) return true;
                                return checkEmailDuplicate(value);
                            }
                        }
                    }
                },
            },
            facilityConfirmEmailId: { required, email, sameAs: sameAs(function () { return this.objfacilities.warehouse.contact.email }) },
            facilities: { checkNullOrEmpty },
            tpsafCountry: { required, checkNullOrEmpty },
            tpsafRegion: { required, checkNullOrEmpty },
            tpsafFacilityCountry: { required, checkNullOrEmpty },
            tpsafFacilityRegion: { required, checkNullOrEmpty }
        },
        methods: {
            initializeTPSAF() {
                var obj = {
                    id: 0, name: '', tinNumber: '', exciseLicenseNumber: '', status: 1,
                    contact: { id: 0, name: '', email: '', countryCode: '+91', phone: '' },
                    address: {
                        id: 0, description: '', postalAddress: '',
                        country: { id: 0, name: '', regions: [] },
                        region: { id: 0, name: '', countryId: 0 },
                    }
                };
                return obj;
            },
            initializeFacility() {
                var obj = {
                    id: 0, warehouse:
                    {
                        id: 0, name: '',
                        contact: { id: 0, name: '', email: '', countryCode: '+91', phone: '' },
                        address: {
                            id: 0, description: '', postalAddress: '',
                            country: { id: 0, name: '', regions: [] },
                            region: { id: 0, name: '', countryId: 0 },
                            longitude: NaN, latitude: NaN
                        },
                    }
                };
                return obj;
            },
            toggleAccordion(no) {
                switch (no) {
                    case 1:
                        this.showAccordion1 = this.showAccordion1 === true ? false : true;
                        this.showAccordion2 = false;
                        break;
                    case 2:
                        this.showAccordion2 = this.showAccordion2 === true ? false : true;
                        this.showAccordion1 = false;
                        break;
                    default:
                        break;
                }
                this.loadAccordionStatus();
            },
            loadAccordionStatus() {
                if (this.tpsaf.name.length > 0)
                    this.accordion1 = true;
                if (this.facilities.length > 0)
                    this.accordion2 = true;
            },
            changeCountry(CountryName) {
                var regions = this.$store.getters.countries.filter(element => element.name === CountryName).map(element => element.regions);
                this.regions = regions[0];
            },
            newTpsaf() {
                this.tpsaf = this.initializeTPSAF();
                this.newTpsafFacility();
                this.facilities = [];
                this.tpsafConfirmEmail = "";
                this.tpsafCountry = this.tpsafRegion = "";
                this.showNewTPSAF = this.showAccordion1 = true;
                this.showAccordion2 = this.accordion1 = this.accordion2 = this.tpsafError = false;
            },
            saveTpsafBascisDetails() {
                this.$v.$touch();
                this.tpsafError = this.$v.tpsaf.$anyError || this.$v.tpsafConfirmEmail.$anyError;
                if (!this.tpsafError) {
                    var country = this.$store.getters.countries.filter(element => element.name === this.tpsafCountry);
                    var region = country[0].regions.filter(element => element.name === this.tpsafRegion);
                    this.tpsaf.address.country = country[0];
                    this.tpsaf.address.region = region[0];
                    this.accordion1 = this.showAccordion2 = true;
                    this.showAccordion1 = false;
                }
            },
            addTpsafFacilityDetails() {
                this.$v.$touch();
                this.facilityWareHouseError = this.$v.objfacilities.warehouse.$anyError || this.$v.facilityConfirmEmailId.$anyError;
                if (!this.facilityWareHouseError) {
                    if (this.facilities.length > 0) {
                        var obj = this.facilities.filter(element => element.warehouse.name === this.objfacilities.warehouse.name);
                        if (obj.length > 0)
                            this.facilities.splice(this.facilities.indexOf(obj[0]), 1);
                    }
                    var country = this.$store.getters.countries.filter(element => element.name === this.tpsafFacilityCountry);
                    var region = country[0].regions.filter(element => element.name === this.tpsafFacilityRegion);
                    this.objfacilities.warehouse.address.country = country[0];
                    this.objfacilities.warehouse.address.region = region[0];
                    this.facilities.push(this.objfacilities);
                    this.objfacilities = this.initializeFacility();
                    this.facilityConfirmEmailId = '';
                    this.showNewFacility = false;
                }
            },
            editTpsafFacility(name) {
                var obj = this.facilities.filter(element => element.warehouse.name == name);
                this.objfacilities = obj[0];
                this.tpsafFacilityCountry = this.objfacilities.warehouse.address.country.name;
                this.changeCountry(this.tpsafFacilityCountry);
                this.tpsafFacilityRegion = this.objfacilities.warehouse.address.region.name;
                this.facilityConfirmEmailId = this.objfacilities.warehouse.contact.email;
                this.showNewFacility = true;
            },
            removeTpsafFacility(name) {
                var obj = this.facilities.filter(element => element.warehouse.name === name);
                if (obj !== null)
                    this.facilities.splice(this.facilities.indexOf(obj[0]), 1);
            },
            async submitAllDetails() {
                var objTpsaf = this.tpsaf;
                objTpsaf.facilities = this.facilities;
                await this.$store.dispatch('addTpsafAsync', objTpsaf).then(() => {
                    toast.success("TPSAF Saved!..");
                    this.bindTpsaf('ALL');
                    this.clearData()
                });
            },
            newTpsafFacility() {
                this.objfacilities = this.initializeFacility();
                this.facilityConfirmEmailId = "";
                this.tpsafFacilityCountry = this.tpsafFacilityRegion = "";
                this.showNewFacility = true; this.facilityWareHouseError = false; this.facilitiesListError = false;
            },
            saveFacilityWarehouse() {
                this.$v.$touch();
                this.facilitiesListError = this.$v.facilities.$anyError;
                if (!this.facilitiesListError) {
                    this.accordion2 = true;
                    this.showAccordion2 = false;
                }
            },
            showPreview() {
                return (this.accordion1 && this.accordion2);
            },
            editTpsafDetails(id) {
                var obj = this.$store.getters.tpsaf.filter(element => element.id === id);
                this.tpsaf = obj[0];
                this.facilities = this.tpsaf.facilities;
                this.tpsafCountry = this.tpsaf.address.country.name;
                this.changeCountry(this.tpsafCountry);
                this.tpsafRegion = this.tpsaf.address.region.name;
                this.tpsafConfirmEmail = this.tpsaf.contact.email;
                this.loadAccordionStatus();
                this.showAccordion1 = this.showNewTPSAF = true;
            },
            async activeDeactivetpsafStatus() {
                await this.$store.dispatch("activateDeactivateTpsafAsync", { orgId: this.deactiveId, orgStatus: this.activeDeactiveStatus });
                this.$root.$emit('bv::hide::modal', 'deactiveTpsaf');
                await this.$store.dispatch("getTpsafAsync");
                await this.bindTpsaf('ALL');
            },
            viewTpsafDetails(tpsId) {
                this.$router.push({ name: 'TpsafView', params: { id: tpsId } });
            },
            clearData() {
                this.accordion1 = this.accordion1 = this.showNewFacility = this.showNewTPSAF = false;
            },
            bindTpsaf(filterStatus) {
                var tpsafs = []; this.tpsafList = [],
                    this.allTpsafCount = this.$store.getters.tpsaf.length;
                this.activeTpsafCount = this.$store.getters.activeTpsaf.length;
                this.inActiveTpsafCount = this.$store.getters.inActiveTpsaf.length;
                switch (filterStatus) {
                    case 'ALL': {
                        this.$store.getters.tpsaf.forEach(element => { tpsafs.push(element) });
                        break;
                    }
                    case 'Active': {
                        this.$store.getters.activeTpsaf.forEach(element => { tpsafs.push(element) });
                        break;
                    }
                    case 'InActive': {
                        this.$store.getters.inActiveTpsaf.forEach(element => { tpsafs.push(element) });
                        break;
                    }
                }
                tpsafs.forEach(element => {
                    var objTpsaf = { id: element.id, name: element.name, tinNumber: element.tinNumber, registerationDate: element.registerationDate, status: element.status };
                    this.tpsafList.push(objTpsaf);
                });
            },
            getactiveDeactiveStatus(id, status) {
                this.deactiveId = id;
                this.activeDeactiveStatus = status;
                this.$root.$emit('bv::show::modal', 'deactiveTpsaf');
            }
        }
    }
</script>